<template>
  <div class="box">
    <div class="playMent" :class="isFixed ? 'fixed' : ''">
      <div @click="$emit('addMusic', songList)">
        <img src="../../assets/images/icon/playMent.png" alt="" />
        <p>
          播放全部<span>({{ topsong.trackCount }})</span>
        </p>
      </div>
      <span><img src="../../assets/images/icon/onMent.png" alt=""/></span>
    </div>
    <div class="list" ref="listHeight">
      <ul>
        <li
          v-for="(item, index) in songList"
          @click="$emit('play', item), (isActive = index)"
          :key="index"
          :style="{ borderColor: isActive == index ? 'red' : '' }"
        >
          <span
            class="left"
            :style="{ color: isActive == index ? 'red' : '' }"
            >{{ index + 1 }}</span
          >
          <div class="content">
            <p>{{ item.name }}</p>
            <p>{{ item.ar[0].name }} - {{ item.al.name }}</p>
          </div>
          <div class="icon">
            <span></span>
          </div>
        </li>
        <p ref="more" class="load">
          <span @click="loadMore" v-if="!isLoading">加载更多...</span>
        </p>
        <Loading v-if="isLoading"></Loading>
      </ul>
    </div>
  </div>
</template>
<style lang="less" scoped>
.fixed {
  position: fixed;
  background-color: white;
  top: 0;
}
.playMent {
  width: 100%;
  height: 48px;
  box-sizing: border-box;
  padding: 3%;
  display: flex;
  div {
    flex: 1;
    img {
      float: left;
      width: 26px;
      margin-right: 14px;
    }
    p {
      line-height: 26px;
      text-align: left;
      font-weight: 600;
      color: #333;
      span {
        font-size: 0.8rem;
        margin-left: 10px;
        color: #afafaf;
      }
    }
  }
  span {
    width: 20%;
    height: 100%;
    img {
      width: 20px;
      margin-top: 4px;
      margin-right: 10px;
      float: right;
    }
  }
}
.list {
  padding: 3%;
  background-color: white;
  box-sizing: border-box;
  padding-bottom: 70px;
  ul {
    width: 100%;
    .load {
      background-color: #dbdbdb;
      color: rgb(75, 75, 75);
      span {
        width: 100%;
        display: block;
      }
    }
    li {
      height: 64px;
      color: #7a7a7a;
      border-bottom: 1px solid;
      border-color: white;
      span.left {
        float: left;
        width: 10%;
        font-size: 1.2rem;
        line-height: 64px;
        height: 100%;
        font-weight: 600;
      }
      div.content {
        width: 78%;
        height: 100%;
        float: left;
        padding-left: 4px;
        p {
          height: 50%;
          text-align: left;
          vertical-align: bottom;
          overflow: hidden;
          font-size: 0.5rem;
          &:first-child {
            font-size: 0.9rem;
            line-height: 38px;
            color: #333;
          }
        }
      }
      div.icon {
        height: 100%;
        width: 10%;
        float: right;
        span {
          width: 100%;
          height: 32px;
          display: inline-block;
          margin-top: 16px;
          background-image: url(../../assets/images/icon/detail.png);
          background-repeat: no-repeat;
          background-position: right;
        }
      }
    }
  }
}
</style>
<script>
import Loading from "@/components/Loading";
export default {
  name: "PlayMent",
  components: { Loading },
  props: ["songId"],
  data() {
    return {
      topsong: [],
      userInfo: [],
      page: 1,
      isLoading: true,
      isActive: null,
      songList: [],
      isFixed: false,
      canRun: true,
      i: 0,
      dataNewSong: [],
    };
  },
  created() {
    this.loadMusic();
  },

  mounted() {
    // window.addEventListener("scroll", this.fixedActiveBtn);
    // console.log(this.$refs.listHeight);
    this.$nextTick(() => {
      window.onscroll = () => {
        // let scrollTop =
        // document.documentElement.scrollTop || document.body.scrollTop;

        //变量scrollTop是滚动条滚动时，距离顶部的距离
        let scrollTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        //变量windowHeight是可视区的高度
        let clientHeight = document.documentElement.clientHeight;
        //变量scrollHeight是滚动条的总高度
        let scrollHeight =
          document.documentElement.scrollHeight || document.body.scrollHeight;
        //滚动条到底部的条件
        if (this.$refs.more != undefined) {
          if (this.i == 0) {
            if (scrollTop + clientHeight >= scrollHeight - 5) {
              this.i++;
              this.loadMore();
            }
          }
        }

        if (document.querySelector(".particulars") != null) {
          let option = scrollTop / 230;

          if (scrollTop <= 230) {
            document.querySelector(
              ".particulars .topnav"
            ).style.backgroundColor = `rgba(255, 255, 255, ${option})`;
            document.querySelector(
              ".particulars .topnav span"
            ).style.color = `rgb(${Math.abs(option * 255 - 255)}, ${Math.abs(
              option * 255 - 255
            )},${Math.abs(option * 255 - 255)})`;
          }
        }
      };
    });
  },
  methods: {
    loadMore() {
      // console.log(this.topsong.trackCount/20);

      if (this.page >= Math.ceil(this.topsong.trackCount / 20)) {
        this.canRun = false;
        this.isLoading = false;
        this.$refs.more.innerText = "没有更多了";
        return;
      }

      if (this.canRun) {
        this.page++;
        this.loadMusic();
        this.i = 0;
      }

      this.isLoading = true;
    },

    // fixedActiveBtn() {
    //   var scrollTop =
    //     window.pageYOffset ||
    //     document.documentElement.scrollTop ||
    //     document.body.scrollTop;
    //   scrollTop >= 250 ? (this.isFixed = true) : (this.isFixed = false);
    //   let parHeight = this.$refs.listHeight.clientHeight;
    //   console.log(parHeight);
    //   if (
    //     window.pageYOffset + document.documentElement.scrollTop >=
    //     parHeight
    //   ) {
    //     // this.loadMore();
    //     // console.log(6666);
    //   } else if (scrollTop <= 0) {
    //     //滚动条距离顶部的高度小于等于0 TODO
    //     //alert("下拉刷新，要在这调用啥方法？");
    //   }
    // },

    loadMusic() {
      this.$axios
        .get("/playlist/detail?id=" + this.songId)
        .then((data) => {
          this.topsong = data.data.playlist;
          this.userInfo = data.data.playlist.creator;
          let trackIDS = data.data.playlist.trackIds.slice(
            (this.page - 1) * 20,
            20 * this.page
          );
          let ids = trackIDS.map((d) => {
            return d.id;
          });
          return ids.join(",");
        })
        .then((ids) => {
          this.$axios.get("/song/detail?ids=" + ids).then((songdata) => {
            this.isLoading = false;
            songdata.data.songs.map((d) => {
              d.song = {
                artists: d.ar,
                // playMaxbr:d.privileges.playMaxbr
              };
              this.songList.push(d);
              // let dataArr = [];
              // for (let i = 0; i < this.data.length; i++) {
              //   dataArr.push({
              //     al: {
              //       picUrl: this.data[i].picUrl,
              //     },
              //     name: this.data[i].name,
              //     ar: [{ name: this.data[i].song.artists[0].name }],
              //     id: this.data[i].id,
              //   });
              // }
              // this.dataNewSong = dataArr;
              return d;
            });
          });
        });
    },
  },
};
</script>
