            position: relative;
          }
          span {
            font-size: 0.5rem;
            margin: 0 10px;
            color: #c7c7c7;
          }
          a {
            background-color: rgba(117, 116, 116, 0.8);
            margin: 0;
            width: 30px;
            // font-size: 1.2rem;
            border-radius: 30px;
            color: white;
            vertical-align: middle;
            display: inline-block;
            img {
              height: 24px;
            }
          }
        }
        .jieshao {
          margin-top: 16px;
          height: 20px;
          position: relative;
          width: 50%;
          float: left;
          &::before {
            content: "";
            position: absolute;
            right: 0px;
            top: 1px;
            width: 16px;
            height: 16px;
            background-size: 100%;
            background-image: url(../assets/images/icon/IntroduceKey.png);
          }
          a {
            width: 90%;
            display: block;
            height: 20px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-family: Arial, Helvetica, sans-serif;
            font-size: 0.8rem;
            color: #c7c7c7;
          }
        }
      }
    }
    .shadeBottom {
      position: absolute;
      z-index: 11;
      left: 0;
      bottom: -1px;
      width: 100%;
    }
    .shade {
      width: 100%;
      position: absolute;
      z-index: -1;
      top: 0;
      filter: blur(20px);
      left: 0;
    }
  }
}
</style>

